html {
  --height-photo: 200px;
  --height-rank: 32px;
  box-sizing: border-box;
  font-family: Arial, Helvetica, sans-serif;
}

body {
  margin: 0;
}

.people {
  grid-template-rows: 231px;
  grid-template-columns: repeat(2, 200px);
  justify-content: center;
  align-items: center;
  gap: 40px 30px;
  max-width: 430px;
  margin: 30px auto;
  display: grid;
}

@media (width >= 1000px) {
  .people {
    grid-template-columns: repeat(4, 200px);
    max-width: 890px;
  }
}

.people :hover {
  transition-duration: 1s;
  transform: scale(1.05);
}

.people :hover img {
  animation: 1s linear forwards round;
}

@keyframes round {
  0% {
    border-radius: 50%;
  }

  100% {
    border-radius: 30%;
  }
}

.people .person {
  border-radius: 10px;
  place-content: center;
}

.people .person .photo {
  height: var(--height-photo);
  width: var(--height-photo);
  margin: 0;
  display: flex;
  position: relative;
}

.people .person .photo img {
  border-radius: 50%;
}

.people .person .photo .rank {
  height: var(--height-rank);
  width: var(--height-rank);
  right: 10px;
  top: calc(var(--height-photo) / 2 - var(--height-rank) / 2);
  color: #8b0000;
  background-color: #fff;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  font-size: 16px;
  font-weight: 700;
  display: flex;
  position: absolute;
}

.people .person .details {
  flex-direction: column;
  display: flex;
}

.people .person .details .name {
  color: #2f4f4f;
  margin: 8px auto 0;
  font-size: 20px;
  font-weight: 700;
  line-height: 24px;
  text-decoration: none;
  display: flex;
}

.people .person .details .extra {
  color: #000;
  visibility: hidden;
  margin: 9px auto;
  font-size: 18px;
  line-height: 20.7px;
  display: flex;
}

.people .person .details .extra .pay:before {
  content: ", $";
}

.people .person .details .extra .pay:after {
  content: "M";
}

.people :hover {
  background-color: #eee;
}

.people :hover .details .extra {
  visibility: visible;
}
